import React from 'react';

const Slider = React.lazy(() => import("./components/slider/Slider"));
const Card = React.lazy(() => import("./components/blindcards/Card"));
const FindRetailer = React.lazy(() => import("./components/findretailer/FindRetailer"));

function Index() {

    return (

        <div className=''>

            <div className="site_body">
                <Slider />
                <h3 className='text-center mt-5 mb-5 text-decoration-underline' style={{ color: "#004170" }}>Patented, Trusted, Australian made</h3>
                <Card />
            </div>

            <div className='mt-5 '>
                <FindRetailer />
            </div>
        </div>

    )
}

export default Index